<template>
  <div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <div class="btn-block">
        <div class="btn-main-deepen" @click="addMeeting">新增</div>
      </div>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="25%">会议名称</th>
          <th width="25%">所在校区</th>
          <th width="25%">状态</th>
          <th width="25%">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item,key) in table.list">
          <td>{{item.name}}</td>
          <td>{{getName(item.campusId)}}</td>
          <td>{{statusGetName(item.status)}}</td>
          <td>
            <span class="oper" @click="detail(item)">编辑</span>
            <span @click="remove(item)" class="oper">删除</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <!--会议室管理-->
      <el-dialog title="新增会议室"
                 :visible.sync="dialogVisible"
                 width="40%"
                 center
                 class="dialog">
        <el-form label-width="120px"
                 v-if="dialogVisible"
                 label-position="left"
                 :model="form"
                 ref="form"
                 :rules="rules">
          <el-form-item label="会议室名称:" prop="name">
            <el-input style="width: 80%" v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="所在校区：" label-width="120px" prop="campusId">
            <el-select placeholder="请选择" style="width: 80%" v-model="form.campusId">
              <el-option v-for="(item, idx) in Campus"
                         :key="idx"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="状态：" prop="status">
            <el-radio-group v-model="form.status">
              <el-radio v-for="(item, idx) in OnlineStatus"
                        :key="idx" border
                        :label="item.value"
                        style="margin-right: 60px">{{item.label}}
              </el-radio>
            </el-radio-group>
          </el-form-item>
        </el-form>
        <div style="text-align: center">
          <div class="btn-submit" @click="submitMeeting">提交</div>
          <div class="btn-clear" style="margin-left: 70px" @click="dialogVisible = false">关闭</div>
        </div>
      </el-dialog>
  </div>
</template>
<script>
  const OnlineStatus = {
    list: [
      {label: '上线', value: 'YES'},
      {label: '下线', value: 'NO'}
    ],
    allList: function () {
      return [{label: '全部', value: null}].concat(this.list)
    },
  }
  export default {
    data () {
      return {
        OnlineStatus: OnlineStatus.list,
        dialogDate: [],
        searchData: [
          {type: 'input', placeholder: '会议主题/关键词'},
          {type: 'select', title: '状态', model: 'status', option: OnlineStatus.allList()},
        ],
        table: {
          url: '/meetingRoom/queryMeetingRoomPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            keywords: null,
            status: 'YES',
          }
        },
        dialogVisible: false,
        meetingSwitch: false,
        // 校区
        Campus: [],
        // 会议室ID
        meetingId: null,
        // 新增会议室
        form: {
          campusId: null,
          name: null,
          status: null
        },
        rules: {
          campusId: {required: true, message: '请选择校区', trigger: 'change'},
          name: [{required: true, message: '请输入会议室名称', trigger: 'blur'},
            {min: 1, max: 40, message: '会议室名称不得超过40个字', trigger: 'blur'}],
          status: {required: true, message: '请选择状态', trigger: 'change'},
        },
      }
    },
    watch: {
      dialogVisible () {
        if (!this.dialogVisible) {
          this.form.campusId = null
          this.form.name = null
          this.form.status = null
        }
      }
    },
    methods: {
      getName: function (val) {
        switch (val) {
          case 1:
            return '南校区'
          case 2:
            return '东校区'
          case 3:
            return '九华校区'
          default:
            return val
        }
      },
      statusGetName: function (val) {
        switch (val) {
          case 'YES':
            return '上线'
          case 'NO':
            return '下线'
          default:
            return val
        }
      },
      // 删除会议室
      async remove (item) {
        await this.$confirm(`删除该会议室，会议室信息将从APP和管理后台均删除，该会议室不可再被预约！`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/meetingRoom/delete.do',
          query: {
            id: item.id
          }
        })
        this.$remind('删除成功')
        this.$search(this.table)
      },
      // 新增会议室
      async submitMeeting () {
        await this.$refs.form.validate()
        let url = '/meetingRoom/add.do'
        let text = '新增会议室成功'
        if (this.meetingSwitch) {
          url = '/meetingRoom/update.do'
          text = '编辑会议室成功'
          this.form.id = this.meetingId
        }
        await this.$ajax({
          url,
          el: 'dialog',
          query: this.form,
        })
        this.$remind(text)
        this.dialogVisible = false
        this.$search(this.table)
      },
      addMeeting () {
        this.dialogVisible = true
      },
      // 编辑会议室
      detail (item) {
        this.form.campusId = item.campusId
        this.form.name = item.name
        this.form.status = item.status
        this.meetingId = item.id
        this.dialogVisible = true
        this.meetingSwitch = true
      },
      // 获取所有校区
      async getCampus () {
        let arr = await this.$ajax({
          url: '/campus/queryCampusList.do'
        })
        arr.forEach(e => {
          this.Campus.push({
            value: e.id,
            label: e.name
          })
        })
      },
    },
    async mounted () {
      this.$search(this.table)
      await this.getCampus()
    }
  }
</script>
<style lang="sass" scoped>
    /deep/ .el-dialog
      width: 40% !important
      border-radius: 10px 10px 0 0
      .el-dialog__header
        background-color: $main-c
        border-radius: 10px 10px 0 0
        .el-dialog__title
          color: #ffffff
        .el-dialog__close
          color: #ffffff
      .dialog_title
        padding: 10px 0
        margin-bottom: 30px
        border-top: 1px solid $border
        border-bottom: 1px solid $border
        text-align: center
        font-size: $font-large
        font-weight: 600
      .dialog_span
        display: block
        margin: 10px 0
        padding: 0 50px
</style>
