<template>
  <div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="16%">会议时间</th>
          <th width="16%">会议主题</th>
          <th width="16%">会议室</th>
          <th width="16%">所在校区</th>
          <th width="16%">状态</th>
          <th width="16%">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item,key) in table.list">
          <td>{{item.meetingTime}}</td>
          <td>{{item.subject}}</td>
          <td>{{item.meetingRoomName}}</td>
          <td>{{item.campusName}}</td>
          <td>{{$enums.MeetingStatus.getName(item.meetingStatus)}}</td>
          <!--<td>{{item.obtainTime | substrTime}}</td>-->
          <td>
            <span class="oper" @click="detail(item)">查看</span>
            <span @click="remove(item)" class="oper">取消</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <!--会议详情-->
      <el-dialog title="会议详情"
                 :visible.sync="dialogVisible"
                 width="40%"
                 center
                 v-if="meetingDetail">
        <span class="dialog_span">会议主题: {{meetingDetail.subject}}</span>
        <span class="dialog_span">会议室: {{meetingDetail.meetingRoomName}}</span>
        <span class="dialog_span">所在校区: {{meetingDetail.campusName}}</span>
        <span class="dialog_span">会议时间: {{meetingDetail.meetingTime}}</span>
        <span class="dialog_span">参与人员: {{meetingDetail.memberList.length}}</span>
        <div class="btns">
          <div class="btn-main"
               v-for="memberItem in meetingDetail.memberList">{{memberItem.name}}
          </div>
        </div>
        <div style="text-align: center">
          <div class="btn-clear" style="margin-left: 20px" @click="dialogVisible = false">关闭</div>
        </div>
      </el-dialog>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        dialogDate: [],
        searchData: [
          {type: 'input', placeholder: '会议主题/关键词'},
          {
            type: 'dateSection', title: '会议时间',
            startDate: new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 30), endDate: new Date(),
            modelName: ['startDate', 'endDate']
          },
          {type: 'select', title: '状态', model: 'meetingStatus', option: this.$enums.MeetingStatus.allList()},
        ],
        table: {
          url: '/meetingOrder/queryPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            keywords: null,
            meetingStatus: 'NOT_STARTED',          // 'NOT_STARTED' 默认查询未召开
            startDate: new Date(new Date().setMonth(new Date().getMonth() - 6)).$Format('yyyy-MM-dd' + ' 00:00:00'),
            endDate: new Date().$Format('yyyy-MM-dd hh:mm:ss')
          }
        },
        dialogVisible: false,
        meetingDetail: null,
        newTableList: []
      }
    },
    // watch: {
    //   'table.list': function () {
    //     this.newTableList.forEach(item =>{
    //       this.newTableList.shift()
    //     })
    //     if (!this.table.list[0]) return
    //     console.log(this.table.list[0])
    //     this.table.list.forEach(e => {
    //       this.newTableList.unshift(e)
    //     })
    //   }
    // },
    methods: {
      // 取消会议
      async remove (item) {
        await this.$confirm(`确认是否取消该预约记录，取消预约后不可被回复？`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/meetingOrder/cancelMeetingOrder.do',
          query: {
            id: item.id
          }
        })
        this.$remind('取消成功')
        this.$search(this.table)
      },
      // 查看会议详情
      detail (item) {
        this.dialogVisible = true
        this.meetingDetail = item
        console.log(this.meetingDetail)
      }
    },
    async mounted () {
      this.$search(this.table)
    }
  }
</script>
