      padding: 10px 0
      margin-bottom: 30px
      border-top: 1px solid $border
      border-bottom: 1px solid $border
      text-align: center
      font-size: $font-large
      font-weight: 600
    .dialog_span
      display: block
      margin: 10px 0
      padding: 0 50px
